@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.default-button{background-color:#8b0303;border:transparent;border-radius:4px;color:white;font-weight:bold;margin-top:.5rem;padding:.5rem}.default-button:hover{background-color:#d80505;color:aliceblue;cursor:pointer}

.list__item{align-items:center;border:1px solid #a5a5a5;border-radius:5px;display:flex;flex-direction:column;justify-content:space-around;min-height:6.25rem;min-width:15.625rem;padding:.625rem}.list__item__header{font-size:1.25rem;font-weight:bold;text-align:center}.list__item__content{font-size:1rem;word-wrap:break-word}@media only screen and (max-width: 750px){.list__item{min-width:12.5rem}}@media only screen and (max-width: 405px){.list__item{min-width:8.75rem}}

.create-new-card form{display:grid;grid-gap:.5rem}.create-new-card form input{height:1.5625rem}.create-new-card form input.textarea{height:3.125rem}.create-new-card form textarea{height:3.125rem}

*{margin:0;padding:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}input,textarea{outline:none}.default-input{border:1px solid #a5a5a5;border-radius:5px;padding:.25rem .375rem}.default-input:hover{border-color:#b8b8b8;background-color:#ececec}html,body{font-size:16px;height:100vh;overflow-x:hidden;width:100vw}#root{font-family:'Roboto', sans-serif;height:100%;width:100%}.main-container{display:grid;grid-gap:1.875rem;grid-template-columns:.5fr 1fr;margin:0rem 5.625rem;margin-top:1.875rem}@media only screen and (max-width: 900px){.main-container{grid-template-columns:1fr}}@media only screen and (max-width: 370px){.main-container{margin:0rem 4.6875rem;margin-top:1.875rem}}.list{display:grid;grid-gap:.625rem;grid-template-columns:1fr 1fr}@media only screen and (max-width: 640px){.list{grid-template-columns:1fr}}

